<template>
  <div class="hello">
    <Navbar :post-title=" movie.title " />
    <div class="container">
    <div class="row">
      <div class="card col" style="width: 18rem;">
        <img :src="movie.image" class="card-img-top" alt="...">
        <div class="card-body">
        <p class="re">Рейтинг: {{ movie.rank }}</p>
        <p class="re">Год выпуска: {{ movie.year }}</p>
        <p class="re">Жанры: {{ movie.genre1 }} {{ movie.genre2 }} {{ movie.genre3 }}</p>
        <p class="re">Время: {{ movie.long }} мин.</p>
        </div>
      </div>
      <div class="col">
        <h5 class="card-title mar">{{ movie.title }}</h5>
        <p>{{ movie.description }}</p>
        <h3 class="re">Смотреть онлайн</h3>
        <span v-html="movie.video" />
      </div>

    </div>
  </div>
    <contact />
  </div>
</template>

<script>
import Navbar from '@/components/Navbar.vue'
import contact from '@/components/contact.vue'
import axios from 'axios'
export default {
  name: 'Film',
  components: {
    Navbar,
    contact
  },
  data () {
    return {
      movie: null,
      filmId: '',
      url: '//mykinodom.ru/movie/film/'
    }
  },
  created () {
    document.title = 'da'
    this.filmId = this.$route.params.id
    this.url = this.url + this.filmId + '/'
    axios
      .get(this.url)
      .then(response => (this.movie = response.data))
  }
}
</script>



